.start h1 {
    font-size: 3.5em;
}

.start p {
    color: yellow;
    margin-bottom: 2em;
}

button{
    background: #1646a0;
    color: #fff;
    padding: 0 45px;
    border: 2px solid #fff;
    border-radius: 25px;
    height: 50px;
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    font-size: 1.2em;
    transition: .4s;
}

button:hover{
    background-color: #0923af;
}